<template>
  <div style="background-color:#f8f8f8">
    <myHeader
      :isBack="true"
      :title="batchNum +'▼'"
      @on-handler-title="selectBatchNum">
    </myHeader>
    
    <div class="seat-head"></div>
    <div class="flex tab ali-c">
      <div class="flex1 flexc">在养头数：{{nowFeedNum}}</div>
      <div class="flex1 flexc" @click="selectType">{{typeName}} <span class="ico-down"></span></div>
    </div>
  
    <div class="page-loadmore-wrapper"
         ref="wrapper"
         :style="{ height: wrapperHeight + 'px' }"
         v-show="collarUseList.length">
      <mt-loadmore :bottom-method="loadBottom"
                   @bottom-status-change="handleBottomChange"
                   :auto-fill="false"
                   :bottom-all-loaded="allLoaded" ref="loadmore">
  
        <list
          :listData='collarUseList'>
        </list>
    
      </mt-loadmore>
    </div>
    <NoRecord v-if="!collarUseList.length"></NoRecord>
  
    <div class="my-prop">
      <!--选择批次-->
      <mt-popup
        v-model="switchBatchNumPopup"
        position="bottom">
        <mt-picker :slots="batchNumSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="batchPicker">
          <span @click="handleBatchConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
  
      <!--选择类型-->
      <mt-popup
        v-model="switchTypePopup"
        position="bottom">
        <mt-picker
          :slots="typeSlots"
          value-key="name"
          :show-toolbar="true"
          ref="typePicker">
          <span @click="handleTypeConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
      
    </div>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import list from '@/components/twolineList'
  import {recipientsRecordsGetBatchNumberList, batchRecordInfo, receivedRecord} from '@/api/finance'
  import NoRecord from "@/components/NoRecord";
  import { Indicator } from 'mint-ui'

  export default {
    name: 'RecipientsRecords',
    components: {
      NoRecord,
      myHeader,
      list
    },
    data() {
      return {
        batchNum: '',
        batchId: '',
        nowFeedNum: '',
        switchBatchNumPopup: false,
        batchNumSlots: [{
          values: [{
            id: '',
            name: ''
          }],
          textAlign: 'center'
        }],
        switchTypePopup: false,
        typeName: '',
        typeId: '',
        typeSlots: [{
          values: [
            {
              id: '1',
              name: '猪只'
            },
            {
              id: '2',
              name: '饲料'
            },
            {
              id: '3',
              name: '兽药'
            },
            {
              id: '4',
              name: '设备'
            },
          ]
        }],
        collarUseList: [],
        pageNumber: 1,
        pageSize: 10,
        allLoaded: false,
        wrapperHeight: 0,
        bottomStatus: '',
      }
    },
    created() {
      this._getBatchListList();
      this.initData();
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
    },
    methods: {
      initData() {
        this.typeId =  this.typeSlots[0].values[0].id;
        this.typeName = this.typeSlots[0].values[0].name;
      },
      _getBatchListList() {
        recipientsRecordsGetBatchNumberList().then(res => {
          if (res.data) {
            this.batchNumSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id
              };
            });
  
            this.batchId = this.batchNumSlots[0].values[0].id;
            this.batchNum = this.batchNumSlots[0].values[0].name;
            this._queryBatchRecordInfo(this.batchId);
            this._receivedRecord({
              type: this.typeId,
              batchId: this.batchId,
              pageNumber: this.pageNumber,
              pageSize: this.pageSize,
            });
          } else {
            Indicator.close();
          }
         
        });
      },
      _queryBatchRecordInfo(batchId) {
        batchRecordInfo(batchId).then(res => {
          let data = res.data;
          this.nowFeedNum = data.curcnt;
        });
      },
      _receivedRecord(data) {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        receivedRecord(data).then(res => {
          Indicator.close();
          if (res.data.details.length) {
            this.collarUseList = res.data.details.map(item => {
              if(this.typeId == '1') {
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '数量：' + item.num,
                  spec: '重量：' + item.weight + 'kg'
                }
              } else if (this.typeId == '2') {
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '金额：' + item.num,
                  spec: '重量：' + item.weight + 'kg'
                }
              } else if (this.typeId == '3') {
                let needNum = item.num - 0;
                needNum = needNum.toFixed(2);
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '金额：' + needNum,
                  spec: '数量：' + item.weight
                }
              } else if (this.typeId == '4') {
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '金额：' + item.num,
                  spec: '数量：' + item.weight
                }
              }
              
            });
            
            if (this.collarUseList.length < this.pageSize) {
              this.$nextTick(() => {
                this.allLoaded = true;
              })
            } else {
              this.$nextTick(() => {
                this.allLoaded = false;
              })
            }
          } else {
            this.collarUseList = [];
          }
        }).catch(err => {
          Indicator.close();
        });
      },
      selectBatchNum() {
        if (this.batchNumSlots[0].values[0].name) {
          this.switchBatchNumPopup = !this.switchBatchNumPopup;
        } else {
          this.$toast({
            message: '无可选批次，请核实业务场景！',
            duration: 1000
          });
        }
      },
      handleBatchConfirm() {
        this.pageNumber = 1;
        this.batchNum = this.$refs.batchPicker.getValues()[0].name;
        this.batchId = this.$refs.batchPicker.getValues()[0].id;
        this.batchId ? this._queryBatchRecordInfo(this.batchId) : null;
        this.typeId && this.batchId ? this._receivedRecord({
          type: this.typeId,
          batchId: this.batchId,
          pageNumber: this.pageNumber,
          pageSize: this.pageSize,
        }) : null;
        this.switchBatchNumPopup = false;
      },
      selectType() {
        this.switchTypePopup = !this.switchTypePopup;
      },
      handleTypeConfirm() {
        this.pageNumber = 1;
        this.typeId = this.$refs.typePicker.getValues()[0].id;
        this.typeName = this.$refs.typePicker.getValues()[0].name;
  
        this.typeId && this.batchId ? this._receivedRecord({
          type: this.typeId,
          batchId: this.batchId,
          pageNumber: this.pageNumber,
          pageSize: this.pageSize,
        }) : null;
  
        this.switchTypePopup = false;
      },
      loadBottom() {
        this.pageNumber++;
        receivedRecord({
          type: this.typeId,
          batchId: this.batchId,
          pageNumber: this.pageNumber,
          pageSize: this.pageSize,
        }).then((res) => {
          let newList = [];
          if (res.data.details.length) {
            newList = res.data.details.map(item => {
              if(this.typeId == '1') {
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '数量：' + item.num,
                  spec: '重量：' + item.weight + 'kg'
                }
              } else if (this.typeId == '2') {
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '金额：' + item.num,
                  spec: '重量：' + item.weight + 'kg'
                }
              } else if (this.typeId == '3') {
                let needNum = item.num - 0;
                needNum = needNum.toFixed(2);
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '金额：' + needNum,
                  spec: '数量：' + item.weight
                }
              } else if (this.typeId == '4') {
                return {
                  nameer: item.reason,
                  recordDate: item.time,
                  price: '金额：' + item.num,
                  spec: '数量：' + item.weight
                }
              }
            });
          }
          
          if (newList.length < this.pageSize) {
            this.allLoaded = true;
          } else {
            this.allLoaded = false;
          }
  
          this.collarUseList = [...this.collarUseList, ...newList];
      
          this.$refs.loadmore.onBottomLoaded();
      
        }, (err) => {
          this.allLoaded = true;
        });
      },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
    }
  }
</script>

<style scoped lang="scss">
  .tab {
    width: 100%;
    height: .4rem;
    font-size: .16rem;
    background-color: #fff;
    .flex1 {
      height: .3rem;
      border-left: 1px solid #f8f8f8;
    }
  }

  .page-loadmore-wrapper {
    overflow: scroll;
    font-size: .16rem;
  }

  .my-prop {
    .mint-popup {
      width: 100%;
      height: 25%;
    }
    /deep/ .picker-toolbar {
      height: 0;
    }
  
    .sure-btn {
      position: absolute;
      top: .05rem;
      right: .05rem;
      padding: .05rem .20rem;
      font-size: .16rem;
      z-index: 99;
    }
  }

</style>



