<template>
    <div>
		<div class="main">
			<div class="list" v-for="(item,index) in listData" :key="index">
				<div class="jus-b top-spec" v-if="item">
					<div>{{item.nameer}}</div>
					<div>{{item.price}}</div>
				</div>
				<div class="jus-b date-param" v-if="item">
					<div>{{item.recordDate}}</div>
					<div>{{item.spec}}</div>
				</div>
			</div>
		</div>
    </div>
</template>
<script>
export default {
    props: {
        listData:{
           type: Array,
           default: () => {
             return []
           }
        }
  },
}
</script>

<style lang="scss" scoped>
.main {
	margin-top: .1rem;
	padding-left: .14rem;
	background-color: #fff;
	.list {
		height: .7rem;
		padding: .1rem .14rem .1rem 0;
		font-size: .16rem;
		color: #000;
		.date-param {
			margin-top: .06rem;
			font-size: .14rem;
			color: #8B8B8D;
		}
	}

}
</style>





